import React, { Component } from 'react';
import { List } from 'antd';
import { Link } from "react-router-dom";
import { myblogData, gojsData, codeReview } from "../../assets/data";
import "./index.less";

export default class index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: []
        };
    }

    componentDidMount() {
        let setData = (dataArray) => {
            let data = [];
            for (let i = 0; i < dataArray.length; i++) {
                data.push({
                    href: dataArray[i].baseHref,
                    className: dataArray[i].name,
                    title: dataArray[i].title,
                    description: dataArray[i].description,
                    img: dataArray[i].img,
                    content: dataArray[i].content,
                })
            }
            return data;
        }

        this.setState({
            dataSource: setData([myblogData, gojsData, codeReview])
        });
    }
    render() {
        return (
            <List
                itemLayout="vertical"
                size="large"
                dataSource={this.state.dataSource}
                renderItem={item => (
                    <Link to={item.href}>
                        <List.Item
                            className="list-item"
                            key={item.title}
                            extra={
                                <div className="box">
                                    <img src={require("../../assets/images/" + item.img)} alt={item.title} />
                                    <div className="box-content">
                                        <span className="title">点击进入</span>
                                    </div>
                                </div >

                            }
                        >
                            <List.Item.Meta
                                title={item.title}
                                description={item.description}
                            />
                            <p style={{ textIndent: "2em", color: "rgba(0, 0, 0, 0.65)" }}>{item.content}</p>
                        </List.Item>
                    </ Link>
                )}
            />
        )
    }
}
